/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Input, Row, Col, Popconfirm, Modal, Spin, Typography, Select, Tag } from 'antd';
import { PlusOutlined, DeleteOutlined, EditOutlined, LoadingOutlined } from '@ant-design/icons';
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from 'react';
import service from '../../services/plan';
import Pagination from '../components/Pagination';
import { AntdMsg } from '../../utils/Antd';

export default function Plan({ type }) {

    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);
    const [qData, setQData] = useState({ page: 1, limit: 20 });
    const addNewModalRef = useRef();


    const columns = [
        {
            title: 'Plan Type',
            dataIndex: 'planType',
            // width: 200,
        },
        {
            title: 'Plan Validity',
            dataIndex: 'planValidity',
            width: 100,
        },
        {
            title: 'Plan Price',
            dataIndex: 'amount',
            // width: 200,
        },
        {
            title: 'Tag',
            dataIndex: 'tag',
            // width: 200,
        },
        {
            title: 'Plan Includes',
            dataIndex: 'planIncludes',
            width: 450,
            render: value => (
                <>
                    <Row gutter={[10, 10]}>
                        <Col>
                            <Button type="primary" size="small">{value[0]}</Button>
                        </Col>
                        <Col>
                            <Button type="primary" size="small">{value[1]}</Button>
                        </Col>
                        <Col>
                            <Button type="primary" size="small">{value[2]}</Button>
                        </Col>
                    </Row>
                </>
            )
        },
        {
            title: 'Status',
            dataIndex: 'status',
            width: 80,
            render: isActive => {
                if (isActive) {
                    return <Tag color='green'>Active</Tag>
                } else {
                    return <Tag color='red'>Inactive</Tag>
                }
            },
        },

        {
            title: 'Action',
            dataIndex: '_id',
            width: 70,
            render: (v, row) => {
                return <>
                    {
                        <Popconfirm
                            title="Are you sure to delete this plan?"
                            onConfirm={() => { deleteData(row._id); }}
                            okText="Yes"
                            cancelText="No"
                        >
                            <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
                        </Popconfirm>
                    }
                    {
                        <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row._id) }} />
                    }
                </>
            }
        },
    ].filter(item => !item.hidden);

    function list() {
        setLoading(true);
        service.list(qData).then(res => {
            setData(res.data?.map((v) => ({ ...v, key: v._id })));
            setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
        }).catch(err => { }).finally(() => {
            setLoading(false);
        });
    }

    const deleteData = (id) => {
        service.delete(id).then(res => {
            AntdMsg(res.message);
            list();
            setSelected([]);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        })
    }

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';

    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, deleteData, qData, setQData, list }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };

    useEffect(() => {
        list();
    }, [qData.page, qData.limit]);

    return (
        <>
            <Typography.Title level={5}>List of  Plans</Typography.Title>

            <Table
                {...tableProps}
                pagination={{ position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            />
            <AddForm ref={addNewModalRef} {...{ list, type }} />

        </>
    );
};

function Search({ addNewModalRef, selected, deleteData, qData, setQData, list }) {



    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col span={5}>
                    {
                        <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                            <Button type="primary" icon={<PlusOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Add New</Button>
                        </Form.Item>
                    }
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete these selected Data?"
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    }

                </Col>
            </Row>
        </Form>
    );
};

const AddForm = forwardRef((props, ref) => {
    const { list } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);




    const handleChange = (value) => {
        Object.entries(value).forEach(ent => {
            let varDt = data;
            const k = ent[0];
            const v = ent[1];
            let keys = k.split('.');
            for (let i = 0; i < keys.length; i++) {
                if (i + 1 === keys.length) {
                    varDt[keys[i]] = v;
                } else {
                    if (typeof varDt[keys[i]] === 'undefined') {
                        if (parseInt(keys[i + 1]) * 1 >= 0) {
                            varDt[keys[i]] = [];
                        } else {
                            varDt[keys[i]] = {};
                        }
                    } varDt = varDt[keys[i]];
                }
            }
        });
        setData({ ...data });
    }

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(
                dt
                    ? { _id: dt }
                    : { status: true }
            );
        }
    }));

    const getDetails = () => {
        setAjxRequesting(true);
        service.detail(data._id).then(res => {
            setData(res.data || {});

        }).catch(err => {
            AntdMsg(err.message, 'error');
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    const save = () => {
        setAjxRequesting(true);
        service.save(data).then((res) => {
            AntdMsg(res.message);
            handleChange({ ...res.data });
            list();
        }).catch(err => {
            if (typeof err.message === 'object') {
                let dt = err.message[Object.keys(err.message)[0]];
                AntdMsg(dt, 'error');
            } else {
                AntdMsg(err.message, 'error');
            }
        }).finally(() => {
            setAjxRequesting(false);
        });

    }

    useEffect(() => {
        if (data._id) {
            getDetails();
        }
    }, [data._id]);

    useEffect(() => {
        if (!open) {
            setData({ _id: null });
        }
    }, [open]);


    const options = [
        {
            label: "Unlimited New Car Post",
            value: "unlimited-new-car-post"
        },
        {
            label: "Unlimited Bidding Post",
            value: "unlimited-bidding-post"
        },
        {
            label: "Unlimited Market Place Post",
            value: "unlimited-market-place-post"
        }
    ];
    return (
        <>
            <Modal
                title={(!data._id ? 'Add' : 'Edit') + ' Plan'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                // destroyOnClose
                maskClosable={false}
                width={1200}
                className="app-modal-body-overflow"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    <Button key="save" type="primary" onClick={save}>Save</Button>,
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Form layout="vertical">
                        <Row gutter={[12, 2]}>
                            <Col span={6}>
                                <Form.Item label="Plan Type" required>
                                    <Select value={data.planType} onChange={v => { handleChange({ planType: v }) }} >
                                        <Select.Option value="Months">Months</Select.Option>
                                        <Select.Option value="Year">Year</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col span={6}>
                                <Form.Item label="Plan Validity" required>
                                    <Input placeholder="5" value={data.planValidity} onChange={e => { handleChange({ planValidity: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Plan Price" required>
                                    <Input placeholder="5000" value={data.amount} onChange={e => { handleChange({ amount: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Plan Includes" required>
                                    <Select
                                        mode="multiple"
                                        allowClear
                                        style={{
                                            width: '100%',
                                        }}
                                        placeholder="Please select the Options"
                                        defaultValue={data.planIncludes}
                                        onChange={(value) => handleChange({ planIncludes: value })}
                                        options={options}
                                    />
                                </Form.Item>
                            </Col>
                            <Col span={10}>
                                <Form.Item label="Tag" required>
                                    <Select value={data.tag} onChange={v => { handleChange({ tag: v }) }} >
                                        <Select.Option value="Super Value">Super Value</Select.Option>
                                        <Select.Option value="Best Value">Best Value</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col span={10}>
                                <Form.Item label="Status" required>
                                    <Select value={data.status} onChange={v => { handleChange({ status: v }) }} >
                                        <Select.Option value={true}>Active</Select.Option>
                                        <Select.Option value={false}>Inactive</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                        </Row>
                    </Form>
                </Spin>

            </Modal>
        </>
    );
});

