/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Input, Row, Col, Popconfirm, Modal, Spin, Typography, Tabs, Card, Select, DatePicker, Divider } from 'antd';
import { PlusOutlined, DeleteOutlined, EditOutlined, LoadingOutlined } from '@ant-design/icons';
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from 'react';
import service from '../../services/car';
import serviceBrand from '../../services/brand';
import serviceModel from '../../services/model';
import serviceDealer from '../../services/dealers';
import moment from "moment";
import Pagination from '../components/Pagination';
import { AntdMsg } from '../../utils/Antd';
import util from '../../utils/util';
import UploadImage from '../../utils/UploadImage';
import UploadVideo from "../../utils/UploadVideo"
import Like from './Like';

export default function Car() {

    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);
    const [qData, setQData] = useState({ page: 1, limit: 20 });
    const addNewModalRef = useRef();
    const likesModalRef = useRef();


    const columns = [
        {
            title: 'Collaboration',
            dataIndex: 'collaboration',
            width: 170,
        },
        {
            title: 'Fuel Type',
            dataIndex: 'fuelType',
            // width: 170,
        },
        {
            title: 'RTO',
            dataIndex: 'rto',
            width: 150,
        },
        {
            title: 'Model',
            dataIndex: 'modelName',
            width: 150,
        },
        {
            title: 'Brand',
            dataIndex: 'brandName',
            width: 150,
        },
        {
            title: 'Asking Price',
            dataIndex: 'askingPrice',
            width: 150,
        },
        {
            title: 'Likes',
            dataIndex: '_id',
            width: 100,
            render: value => (
                <>
                    <Button type="primary" size="small" onClick={() => { likesModalRef.current.openForm({ _id: value }) }} >Likes</Button>
                </>
            )
        },
        {
            title: 'Action',
            dataIndex: '_id',
            width: 70,
            render: (v, row) => {
                return <>
                    <Popconfirm
                        title="Are you sure to delete this Car?"
                        onConfirm={() => { deleteData(row._id); }}
                        okText="Yes"
                        cancelText="No"
                    >
                        <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
                    </Popconfirm>
                    <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row._id) }} />
                </>
            }
        },
    ].filter(item => !item.hidden);

    function list() {
        setLoading(true);
        service.list(qData).then(res => {
            setData(res.data?.map((v) => ({ ...v, key: v._id })));
            setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
        }).catch(err => { }).finally(() => {
            setLoading(false);
        });
    }

    const deleteData = (id) => {
        service.delete(id).then(res => {
            AntdMsg(res.message);
            list();
            setSelected([]);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        })
    }

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';

    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, deleteData, qData, setQData, list, data }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };

    useEffect(() => {
        list();
    }, [qData.page, qData.limit]);

    return (
        <>
            {
                <Typography.Title level={5}>List of Cars</Typography.Title>
            }

            <Table
                {...tableProps}
                pagination={{ position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            />
            <AddForm ref={addNewModalRef} {...{ list }} />
            <LikesModal ref={likesModalRef} />


        </>
    );
};

function Search({ addNewModalRef, selected, deleteData, qData, setQData, list, data }) {

    const [modelNames, setModelNames] = useState([])

    const onChange = (v, key) => {
        qData[key] = v;
        if (v === undefined) {
            qData[key] = "";
            list()
        }

    }


    useEffect(() => {
        const models = data?.map((item) => (
            {
                label: item.modelName,
                value: item.modelId,
            }

        ))
        setModelNames(models)

    }, [data])

    
    

    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col span={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Select value={data.fuelType} onChange={(v) => onChange(v, "fuelType")} placeholder="Select by Fuel type" allowClear>
                            <Select.Option value="Diesel" >Diesel</Select.Option>
                            <Select.Option value="Petrol" >Petrol</Select.Option>
                            <Select.Option value="Petrol + CNG" >Petrol + CNG</Select.Option>
                            <Select.Option value="Petrol + LPG" >Petrol + LPG</Select.Option>
                            <Select.Option value="Electric" >Electric</Select.Option>
                            <Select.Option value="Hybrid" >Hybrid</Select.Option>
                        </Select>

                    </Form.Item>
                </Col>
                <Col span={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Select
                            showSearch
                            allowClear
                            placeholder="Select by Model Name"
                            optionFilterProp="children"
                            onChange={(v) => onChange(v, "modelId")}
                            filterOption={(input, option) =>
                                (option?.label ?? '').toLowerCase().includes(input.toLowerCase())
                            }
                            options={modelNames}
                        />

                    </Form.Item>
                </Col>
                <Col span={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Button type="primary" htmlType="submit">Search</Button>
                    </Form.Item>
                </Col>
                <Col span={5}>
                    <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                        <Button type="primary" icon={<PlusOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Add New</Button>
                    </Form.Item>
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete these selected dealer?"
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    }

                </Col>
            </Row>
        </Form>
    );
};

const AddForm = forwardRef((props, ref) => {
    const { list } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);
    const [currentTab, setCurrentTab] = useState(0);
    const [enginePhotoList, setEnginePhotoList] = useState([])
    const [engineVideoList, setEngineVideoList] = useState([]);
    const [exteriorPhotoList, setExteriorPhotoList] = useState([]);
    const [exteriorVideoList, setExteriorVideoList] = useState([])
    const [interiorPhotoList, setInteriorPhotoList] = useState([]);
    const [interiorVideoList, setInteriorVideoList] = useState([])
    const [rcPhotoList, setRcPhotoList] = useState([])
    const [chassisPhotoList, setChassisPhotoList] = useState([])
    const [insurancePhotoList, setInsurancePhotoList] = useState([])
    const [additionalPhotoList, setAdditionalPhotoList] = useState([])



    const handleChange = (value) => {
        Object.entries(value).forEach(ent => {
            let varDt = data;
            const k = ent[0];
            const v = ent[1];
            let keys = k.split('.');
            for (let i = 0; i < keys.length; i++) {
                if (i + 1 === keys.length) {
                    varDt[keys[i]] = v;
                } else {
                    if (typeof varDt[keys[i]] === 'undefined') {
                        if (parseInt(keys[i + 1]) * 1 >= 0) {
                            varDt[keys[i]] = [];
                        } else {
                            varDt[keys[i]] = {};
                        }
                    } varDt = varDt[keys[i]];
                }
            }
        });
        setData({ ...data });
    }

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(
                dt
                    ? { _id: dt }
                    : { status: true }
            );
        }
    }));

    const getDetails = () => {
        setAjxRequesting(true);
        service.detail(data._id).then(res => {
            setData(res.data || {});
            setEnginePhotoList(res.data?.enginePhotoList || []);
            setEngineVideoList(res.data?.engineVideoList || []);
            setExteriorPhotoList(res.data?.exteriorPhotoList || []);
            setExteriorVideoList(res.data?.exteriorVideoList || []);
            setInteriorPhotoList(res.data?.interiorPhotoList || []);
            setInteriorVideoList(res.data?.interiorVideoList || []);
            setRcPhotoList(res.data?.rcPhotoList || []);
            setChassisPhotoList(res.data?.chassisPhotoList || []);
            setInsurancePhotoList(res.data?.insurancePhotoList || []);
            setAdditionalPhotoList(res.data?.AdditionalPhotoList || []);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    const save = () => {
        setAjxRequesting(true);
        try {
            (() => {
                return currentTab === 0
                    ? service.saveOne(data)
                    : currentTab === 1
                        ? service.saveTwo(data._id, data)
                        : currentTab === 2
                            ? service.saveThree(data._id, data)
                            : service.saveFour(data._id, data);

            })().then(res => {
                AntdMsg(res.message);
                handleChange({ ...res.data });
                setCurrentTab((currentTab + 1) % 4);
                list();
            }).catch(err => {
                if (typeof err.message === 'object') {
                    let dt = err.message[Object.keys(err.message)[0]];
                    AntdMsg(dt, 'error');
                } else {
                    AntdMsg(err.message, 'error');
                }
            }).finally(() => {
                setAjxRequesting(false);
            });
        } catch (err) {
            AntdMsg(err.message, 'error');
        }
    }

    useEffect(() => {
        if (data._id) {
            getDetails();
        }
    }, [currentTab, data._id]);

    useEffect(() => {
        if (!open) {
            setData({ _id: null });
        } else {
            setCurrentTab(0);
            setEnginePhotoList([]);
            setEngineVideoList([]);
            setExteriorPhotoList([]);
            setExteriorVideoList([]);
            setInteriorPhotoList([]);
            setInteriorVideoList([]);
            setRcPhotoList([]);
            setChassisPhotoList([]);
            setInsurancePhotoList([]);
            setAdditionalPhotoList([]);
        }
    }, [open]);

    useEffect(() => {
        handleChange({ enginePhotoList });
        handleChange({ engineVideoList });
        handleChange({ exteriorPhotoList });
        handleChange({ exteriorVideoList });
        handleChange({ interiorPhotoList });
        handleChange({ interiorVideoList });
        handleChange({ rcPhotoList });
        handleChange({ chassisPhotoList });
        handleChange({ insurancePhotoList });
        handleChange({ AdditionalPhotoList: additionalPhotoList });
    }, [engineVideoList, enginePhotoList, exteriorPhotoList, exteriorVideoList, interiorPhotoList, interiorVideoList, rcPhotoList, chassisPhotoList, insurancePhotoList, additionalPhotoList]);

    return (
        <>
            <Modal
                title={(!data._id ? 'Add' : 'Edit') + ' Car'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                // destroyOnClose
                maskClosable={false}
                width={1200}
                className="app-modal-body-overflow"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    <Button key="save" type="primary" onClick={save}>{currentTab === 3 ? "Save" : "Next"}</Button>
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Tabs activeKey={currentTab} type="card" size="small" style={{ marginBottom: 0 }} onChange={(v) => { setCurrentTab(v); }}
                        items={[
                            {
                                label: "Basic Details",
                                key: 0,
                                children: <Card style={{ borderTop: 0 }}>
                                    <BasicDetails {...{ data, handleChange, save }} />
                                </Card>,
                            },
                            {
                                label: "Car Info",
                                key: 1,
                                children: <Card style={{ borderTop: 0 }}>
                                    <CarInfo {...{ data, handleChange, save }} />
                                </Card>,
                                disabled: !data._id
                            },
                            {
                                label: "Photos/Videos",
                                key: 2,
                                children: <Card style={{ borderTop: 0 }}>
                                    <Photos {...{ data, handleChange, save, enginePhotoList, engineVideoList, setEngineVideoList, setEnginePhotoList, exteriorPhotoList, setExteriorPhotoList, exteriorVideoList, setExteriorVideoList, interiorPhotoList, setInteriorPhotoList, interiorVideoList, setInteriorVideoList, rcPhotoList, setRcPhotoList, chassisPhotoList, setChassisPhotoList, insurancePhotoList, setInsurancePhotoList, additionalPhotoList, setAdditionalPhotoList }} />
                                </Card>,
                                disabled: !data._id
                            },
                            {
                                label: "Registration Details",
                                key: 3,
                                children: <Card style={{ borderTop: 0 }}>
                                    <RegistrationDetails {...{ data, handleChange, save }} />
                                </Card>,
                                disabled: !data._id
                            }
                        ]}
                    />
                </Spin>

            </Modal>
        </>
    );
});

const BasicDetails = ({ data, handleChange }) => {
    const [brand, setBrand] = useState([])
    const [model, setModel] = useState([])
    const [dealers, setDealers] = useState([])
    useEffect(() => {
        serviceBrand.listBrand().then(res => {
            setBrand(res.data);
        })
            .catch(err => {
                if (typeof err.message === 'object') {
                    let dt = err.message[Object.keys(err.message)[0]];
                    AntdMsg(dt, 'error');
                } else {
                    AntdMsg(err.message, 'error');
                }
            })
    }, [])
    useEffect(() => {
        const query = { brandId: data?.brandId };
        serviceModel.listModel(query).then(res => {
            setModel(res.data)
        })
            .catch(err => {
                if (typeof err.message === 'object') {
                    let dt = err.message[Object.keys(err.message)[0]];
                    AntdMsg(dt, 'error');
                } else {
                    AntdMsg(err.message, 'error');
                }
            })
    }, [data.brandId])

    useEffect(() => {
        serviceDealer.list().then(res => {
            setDealers(res.data);
        })
            .catch(err => {
                if (typeof err.message === 'object') {
                    let dt = err.message[Object.keys(err.message)[0]];
                    AntdMsg(dt, 'error');
                } else {
                    AntdMsg(err.message, 'error');
                }
            })
    }, [])


    return (
        <>
            <Form layout="vertical">
                <Row gutter={[12, 2]}>
                    <Col span={6}>
                        <Form.Item label="Dealer Name" required>
                            <Select value={data.dealerId} onChange={v => { handleChange({ dealerId: v }) }} >
                                {
                                    dealers.map(item => <Select.Option value={item._id} key={item._id}>{item.userName}</Select.Option>)
                                }
                            </Select>
                        </Form.Item>
                    </Col>
                    <Col span={12}>
                        <Form.Item label="Asking Price" required>
                            <Input placeholder="₹500000" value={data.askingPrice} onChange={e => { handleChange({ askingPrice: e.target.value }) }} />
                        </Form.Item>
                    </Col>
                    <Col span={6}>
                        <Form.Item label="Brand" required>
                            <Select value={data.brandId} onChange={v => { handleChange({ brandId: v, modelId: null }) }} >
                                {brand.map(item => <Select.Option key={item._id} value={item._id} >{item.name}</Select.Option>)}
                            </Select>
                        </Form.Item>
                    </Col>
                    <Row gutter={[12, 2]}>
                        <Col span={6}>
                            <Form.Item label="Models" required>
                                <Select value={data.modelId} onChange={v => { handleChange({ modelId: v }) }} >
                                    {model.map(item => <Select.Option key={item._id} value={item._id} >{item.name}</Select.Option>)}
                                </Select>
                            </Form.Item>

                        </Col>
                        <Col span={6}>
                            <Form.Item label="Manufacturing Month" required>
                                <DatePicker
                                    picker='month'
                                    className="w-100"
                                    value={moment(data?.manufacturingMonth)}
                                    format="MMMM"
                                    onChange={(v) => { handleChange({ manufacturingMonth: v }) }} allowClear={false} />
                            </Form.Item>
                        </Col>
                        <Col span={6}>
                            <Form.Item label="Manufacturing Year" required>
                                <DatePicker
                                    picker='year'
                                    className="w-100"
                                    value={moment(data?.manufacturingYear)}
                                    onChange={(v) => { handleChange({ manufacturingYear: v }) }} allowClear={false} />
                            </Form.Item>
                        </Col>
                        <Col span={6}>
                            <Form.Item label="Varient" required>
                                <Input placeholder="varient" value={data.varient} onChange={e => { handleChange({ varient: e.target.value }) }} />
                            </Form.Item>
                        </Col>
                        <Col span={6}>
                            <Form.Item label="Km's Driven" required>
                                <Input type="text" placeholder="500 Km" value={data.kmsDrive} onChange={e => { handleChange({ kmsDrive: util.handleInteger(e.target.value) }) }} />
                            </Form.Item>
                        </Col>
                        <Col span={6}>
                            <Form.Item label="Fuel Type" required>
                                <Select value={data.fuelType} onChange={v => { handleChange({ fuelType: v }) }} >
                                    <Select.Option value="Diesel" >Diesel</Select.Option>
                                    <Select.Option value="Petrol" >Petrol</Select.Option>
                                    <Select.Option value="Petrol + CNG" >Petrol + CNG</Select.Option>
                                    <Select.Option value="Petrol + LPG" >Petrol + LPG</Select.Option>
                                    <Select.Option value="Electric" >Electric</Select.Option>
                                    <Select.Option value="Hybrid" >Hybrid</Select.Option>
                                </Select>
                            </Form.Item>
                        </Col>
                        <Col span={6}>
                            <Form.Item label="Transmission Type" required>
                                <Select value={data.transmissionType} onChange={v => { handleChange({ transmissionType: v }) }} >
                                    <Select.Option value="Automatic" >Automatic</Select.Option>
                                    <Select.Option value="Manual" >Manual</Select.Option>
                                </Select>
                            </Form.Item>
                        </Col>
                        <Col span={6}>
                            <Form.Item label="Number of Keys" required>
                                <Select value={data.keys} onChange={v => { handleChange({ keys: v }) }} >
                                    <Select.Option value="1" >1</Select.Option>
                                    <Select.Option value="2" >2</Select.Option>
                                    <Select.Option value="3" >3</Select.Option>
                                </Select>
                            </Form.Item>
                        </Col>
                    </Row>
                </Row>
            </Form>
        </>
    );
}

const CarInfo = ({ data, handleChange }) => {


    return (
        <>
            <Form layout="vertical">
                <Row gutter={[12, 2]}>
                    <Col span={6}>
                        <Form.Item label="Vehicle Color" required>
                            <Input placeholder="Black" value={data.vehicleColor} onChange={e => { handleChange({ vehicleColor: e.target.value }) }} />
                        </Form.Item>
                    </Col>
                    <Col span={6}>
                        <Form.Item label="RTO" required>
                            <Input placeholder="MH-19" value={data.rto} onChange={e => { handleChange({ rto: e.target.value }) }} />
                        </Form.Item>
                    </Col>
                    <Col span={6}>
                        <Form.Item label="Registration State" required>
                            <Input placeholder="Maharashtra" value={data.registrationState} onChange={e => { handleChange({ registrationState: e.target.value }) }} />
                        </Form.Item>
                    </Col>
                    <Col span={6}>
                        <Form.Item label="Registration City" required>
                            <Input placeholder="Mumbai" value={data.registrationCity} onChange={e => { handleChange({ registrationCity: e.target.value }) }} />
                        </Form.Item>
                    </Col>
                    <Col span={6}>
                        <Form.Item label="Number Of Owners" required>
                            <Input placeholder="1" value={data.numberOfOwners} onChange={e => { handleChange({ numberOfOwners: e.target.value }) }} />
                        </Form.Item>
                    </Col>
                    <Col span={6}>
                        <Form.Item label="Insurance Valid till Month" required>
                            <DatePicker
                                picker='month'
                                className="w-100"
                                value={moment(data?.insuranceValidTillMonth)}
                                format="MMMM"
                                onChange={(v) => { handleChange({ insuranceValidTillMonth: v }) }} allowClear={false} />
                        </Form.Item>
                    </Col>
                    <Col span={6}>
                        <Form.Item label="Insurance Valid till Year" required>
                            <DatePicker
                                picker='year'
                                className="w-100"
                                value={moment(data?.insuranceValidTillYear)}
                                onChange={(v) => { handleChange({ insuranceValidTillYear: v }) }} allowClear={false} />
                        </Form.Item>
                    </Col>
                    <Col span={6}>
                        <Form.Item label="Type of Insurance" required>
                            <Select value={data.typeOfInsurance} onChange={v => { handleChange({ typeOfInsurance: v }) }} >
                                <Select.Option value="Comprehensive" >Comprehensive</Select.Option>
                                <Select.Option value="Zero Depreciation" >Zero Depreciation</Select.Option>
                                <Select.Option value="3rd Party" >3rd Party</Select.Option>
                                <Select.Option value="Insurance Expired" >Insurance Expired</Select.Option>
                            </Select>
                        </Form.Item>
                    </Col>
                    <Col span={6}>
                        <Form.Item label="No Claim Bonus" required>
                            <Select value={data.noClaimBonus} onChange={v => { handleChange({ noClaimBonus: v }) }} >
                                <Select.Option value={true} >Yes</Select.Option>
                                <Select.Option value={false} >No</Select.Option>
                            </Select>
                        </Form.Item>
                    </Col>
                    <Col span={6}>
                        <Form.Item label="No Claim Bonus Percentage(%)" required>
                            <Input placeholder="1" type="number" value={data.noClaimBonusPercentage} onChange={e => { handleChange({ noClaimBonusPercentage: e.target.value }) }} />
                        </Form.Item>
                    </Col>
                </Row>
            </Form>
        </>
    );
}

const Photos = ({ data, handleChange, enginePhotoList, setEnginePhotoList, exteriorPhotoList, setExteriorPhotoList, exteriorVideoList, setExteriorVideoList, interiorPhotoList, setInteriorPhotoList, setInteriorVideoList, interiorVideoList, engineVideoList, setEngineVideoList, rcPhotoList, setRcPhotoList, chassisPhotoList, setChassisPhotoList, insurancePhotoList, setInsurancePhotoList, additionalPhotoList, setAdditionalPhotoList }) => {


    return (
        <>
            <Form layout="vertical">
                <Row gutter={[12, 2]}>
                    <Col span={24}>
                        <Divider orientation="left" plain={true}><Typography.Text type="danger">Engine Photos and Videos</Typography.Text></Divider>
                    </Col>
                    <Col span={24}>
                        <Form.Item label="Photos">
                            <UploadImage {...{ fileList: enginePhotoList, setFileList: setEnginePhotoList }} count={10} />
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Form.Item label="Videos">
                            <UploadVideo {...{ fileList: engineVideoList, setFileList: setEngineVideoList }} count={10} />
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Divider orientation="left" plain={true}><Typography.Text type="danger">Exterior Photos and Videos</Typography.Text></Divider>
                    </Col>
                    <Col span={24}>
                        <Form.Item label="Photos">
                            <UploadImage {...{ fileList: exteriorPhotoList, setFileList: setExteriorPhotoList }} count={10} />
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Form.Item label="Videos">
                            <UploadVideo {...{ fileList: exteriorVideoList, setFileList: setExteriorVideoList }} count={10} />
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Divider orientation="left" plain={true}><Typography.Text type="danger">Interior Photos and Videos</Typography.Text></Divider>
                    </Col>
                    <Col span={24}>
                        <Form.Item label="Photos">
                            <UploadImage {...{ fileList: interiorPhotoList, setFileList: setInteriorPhotoList }} count={10} />
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Form.Item label="Videos">
                            <UploadVideo {...{ fileList: interiorVideoList, setFileList: setInteriorVideoList }} count={10} />
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Divider orientation="left" plain={true}><Typography.Text type="danger">RC Photos </Typography.Text></Divider>
                    </Col>
                    <Col span={24}>
                        <Form.Item label="Photos">
                            <UploadImage {...{ fileList: rcPhotoList, setFileList: setRcPhotoList }} count={10} />
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Divider orientation="left" plain={true}><Typography.Text type="danger">Chassis Photos </Typography.Text></Divider>
                    </Col>
                    <Col span={24}>
                        <Form.Item label="Photos">
                            <UploadImage {...{ fileList: chassisPhotoList, setFileList: setChassisPhotoList }} count={10} />
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Divider orientation="left" plain={true}><Typography.Text type="danger">Insurance Photos </Typography.Text></Divider>
                    </Col>
                    <Col span={24}>
                        <Form.Item label="Photos">
                            <UploadImage {...{ fileList: insurancePhotoList, setFileList: setInsurancePhotoList }} count={10} />
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Divider orientation="left" plain={true}><Typography.Text type="danger">Additional Photos </Typography.Text></Divider>
                    </Col>
                    <Col span={24}>
                        <Form.Item label="Photos">
                            <UploadImage {...{ fileList: additionalPhotoList, setFileList: setAdditionalPhotoList }} count={10} />
                        </Form.Item>
                    </Col>
                </Row>
            </Form>
        </>
    );
}

const RegistrationDetails = ({ data, handleChange }) => {


    return (
        <>
            <Form layout="vertical">
                <Row gutter={[12, 2]}>
                    <Col span={6}>
                        <Form.Item label="Registration Number" required>
                            <Input placeholder="01010101010101" value={data.registrationNumber} onChange={e => { handleChange({ registrationNumber: e.target.value }) }} />
                        </Form.Item>
                    </Col>
                    <Col span={6}>
                        <Form.Item label="Ownership Type" required>
                            <Input placeholder="Black" value={data.ownershipType} onChange={e => { handleChange({ ownershipType: e.target.value }) }} />
                        </Form.Item>
                    </Col>
                    <Col span={6}>
                        <Form.Item label="Chassis Number" required>
                            <Input placeholder="Chassis Number" value={data.chassisNumber} onChange={e => { handleChange({ chassisNumber: e.target.value }) }} />
                        </Form.Item>
                    </Col>

                    <Col span={6}>
                        <Form.Item label="RC avaibility" required>
                            <Select value={data.rcAvailibity} onChange={v => { handleChange({ rcAvailibity: v }) }} >
                                <Select.Option value="Original" >Original</Select.Option>
                                <Select.Option value="Duplicate" >Duplicate</Select.Option>
                                <Select.Option value="Lost" >Lost</Select.Option>
                            </Select>
                        </Form.Item>
                    </Col>
                    <Col span={6}>
                        <Form.Item label="Chassis Number Embossing" required>
                            <Select value={data.chassisNumberEmbossing} onChange={v => { handleChange({ chassisNumberEmbossing: v }) }} >
                                <Select.Option value="OK" >OK</Select.Option>
                                <Select.Option value="Rusted" >Rusted</Select.Option>
                                <Select.Option value="Repunched" >Repunched</Select.Option>
                                <Select.Option value="No Traceable" >No Traceable</Select.Option>
                                <Select.Option value="Mis-Match" >Mis-Match</Select.Option>
                                <Select.Option value="Incomplete" >Incomplete</Select.Option>
                            </Select>
                        </Form.Item>
                    </Col>
                </Row>
            </Form>
        </>
    );
}



const LikesModal = forwardRef((props, ref) => {
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });


    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(dt);
        }
    }));
    return (
        <>
            <Modal
                title="Likes Details"
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onCancel={() => { setOpen(false); }}
                maskClosable={false}
                width={1400}
                destroyOnClose
                className="app-modal-body-overflow"
                footer={null}
            >
                <Like carId={data._id} />
            </Modal>
        </>
    );
});
