import axios from "../utils/axios";

export default class service {

    static baseURL = 'dealer';

    static list(data) {
        return axios.get(this.baseURL + '/listDealers', { params: data });
    }
    static listFollowers(data) {
        return axios.get(this.baseURL + '/followers/list', { params: data });
    }
    static listBookmarks(data) {
        return axios.get(this.baseURL + '/bookmarks/list', { params: data });
    }
    static detail(id, data) {
        return axios.get(this.baseURL + '/details/' + id, { params: data });
    }
    static save(data) {
        return axios.post(this.baseURL + "/saveDealer", data);
    }
    static delete(id) {
        return axios.post(this.baseURL + '/delete', { ids: id });
    }
}