/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Input, Row, Col, Popconfirm, Modal, Spin, Typography, Tag, Select, DatePicker } from 'antd';
import { PlusOutlined, DeleteOutlined, EditOutlined, LoadingOutlined } from '@ant-design/icons';
import moment from "moment";
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from 'react';
import service from '../../services/dealers';
import Pagination from '../components/Pagination';
import { AntdMsg } from '../../utils/Antd';
import util from '../../utils/util';
import UploadImage from '../../utils/UploadImage';
import Followers from './Followers';
import Bookmarks from './Bookmarks';

export default function Dealers() {

    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);
    const [qData, setQData] = useState({ page: 1, limit: 20 });

    const addNewModalRef = useRef();
    const followersModalRef = useRef();
    const bookmarkModalRef = useRef();

    const columns = [
        {
            title: 'Username',
            dataIndex: 'userName',
            width: 150,


        },
        {
            title: 'Phone',
            dataIndex: 'phone',
            width: 150,
        },
        {
            title: 'Email',
            dataIndex: 'email',
            width: 150,
        },
        {
            title: 'Address',
            dataIndex: 'address',
            width: 300,
        },
        {
            title: 'Bookmarks',
            dataIndex: '_id',
            width: 100,
            render: value => (
                <>
                    <Button type="primary" size="small" onClick={() => { bookmarkModalRef.current.openForm({ _id: value }) }} >Bookmarks</Button>
                </>
            )
        },
        {
            title: 'Followers',
            dataIndex: '_id',
            width: 100,
            render: value => (
                <>
                    <Button type="primary" size="small" onClick={() => { followersModalRef.current.openForm({ _id: value }) }} >Followers</Button>
                </>
            )
        },
        {
            title: 'Status',
            dataIndex: 'status',
            width: 80,
            render: isActive => {
                if (isActive) {
                    return <Tag color='green'>Active</Tag>
                } else {
                    return <Tag color='red'>Inactive</Tag>
                }
            },
        },
        {
            title: 'Action',
            dataIndex: '_id',
            width: 70,
            render: (v, row) => {
                return <>
                    <Popconfirm
                        title="Are you sure to delete this dealer?"
                        onConfirm={() => { deleteData(row._id); }}
                        okText="Yes"
                        cancelText="No"
                    >
                        <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
                    </Popconfirm>
                    <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row) }} />
                </>
            }
        },
    ];

    function list() {
        setLoading(true);
        service.list(qData).then(res => {
            setData(res.data?.map((v) => ({ ...v, key: v._id })));
            setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
        }).catch(err => { }).finally(() => {
            setLoading(false);
        });
    }

    const deleteData = (id) => {
        service.delete(id).then(res => {
            AntdMsg(res.message);
            list();
            setSelected([]);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        })
    }

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';

    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, deleteData, qData, setQData, list, users: data }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };

    useEffect(() => {
        list();
    }, [qData.page, qData.limit]);



    useEffect(() => {
        if (qData.key === "") {
            list()
        }
    }, [qData.key])



    return (
        <>
            <Typography.Title level={5}>List of Dealers</Typography.Title>
            <Table
                {...tableProps}
                pagination={{ position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            // minWidth="500px"
            />
            <AddForm ref={addNewModalRef} {...{ list }} />
            <FollowersModal ref={followersModalRef} />
            <BookmarkModal ref={bookmarkModalRef} />

        </>
    );
};

function Search({ addNewModalRef, selected, deleteData, qData, setQData, list, users }) {

    const [userNames, setUserNames] = useState([]);
    const onChange = (value) => {
        setQData({ ...qData, key: value })
        if (value === undefined) {
            setQData({ ...qData, key: "" })
            list();
        }
    }

    useEffect(() => {
        const Names = users?.map((item) => (
            {
                label: item.userName ? <><p style={{ padding: "0px", margin: "0px" }}>{item.userName}</p><p style={{ fontSize: "12px", margin: "0px" }}><Typography.Text type="danger">@{item.phone}</Typography.Text></p></> : item.phone,
                value: item.userName ? item.userName : item.phone,
                searchKey: item.userName + item.phone
            }

        ))
        setUserNames(Names)
    }, [users])


    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col span={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Select
                            showSearch
                            allowClear
                            placeholder="Select by username / phone"
                            optionFilterProp="children"
                            onChange={(e) => onChange(e)}
                            filterOption={(input, option) =>
                                (option?.searchKey ?? '').toLowerCase().includes(input.toLowerCase())
                            }
                            options={userNames}
                        />

                    </Form.Item>
                </Col>
                <Col span={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Button type="primary" htmlType="submit">Search</Button>
                    </Form.Item>
                </Col>
                <Col span={5}>
                    <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                        <Button type="primary" icon={<PlusOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Add New</Button>
                    </Form.Item>
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete these selected dealer?"
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    }

                </Col>
            </Row>
        </Form>
    );
};

const AddForm = forwardRef((props, ref) => {
    const { list } = props;
    const [ajxRequesting, setAjxRequesting] = useState(false);
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({});
    const [fileList, setFileList] = useState([]);

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(dt ? { ...dt } : { status: true });
            if (dt && dt.avatar) {
                setFileList([{ ...dt.avatar }]);
            } else {
                setFileList([]);
            }
        }
    }));

    const handleChange = (v, k) => { setData({ ...data, [k]: v }); }

    const save = () => {
        setAjxRequesting(true);

        service.save(data).then((res) => {
            AntdMsg(res.message);
            setOpen(false);
            list();
        }).catch(err => {
            if (typeof err.message === 'object') {
                let dt = err.message[Object.keys(err.message)[0]];
                AntdMsg(dt, 'error');
            } else {
                AntdMsg(err.message, 'error');
            }
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    useEffect(() => {
        if (!data._id) {
            handleChange(util.removeSpecialChars(data?.name || ''), 'userName');
        }
    }, [data.name])

    useEffect(() => {
        handleChange(fileList?.[0], 'avatar');
    }, [fileList]);

    return (
        <>
            <Modal
                title={(!data._id ? 'Add' : 'Edit') + ' Dealer'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={800}
                className="app-modal-body-overflow"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    <Button key="save" type="primary" onClick={save}>Save</Button>,
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Form layout="vertical">
                        <Row gutter={[12, 2]}>
                            <Col span={20}>
                                <Row gutter={[12, 2]}>
                                    <Col span={12}>
                                        <Form.Item label="Username" required>
                                            <Input placeholder="John Doe" value={data.userName} onChange={e => { handleChange(e.target.value, 'userName') }} />
                                        </Form.Item>
                                    </Col>

                                    <Col span={12}>
                                        <Form.Item label="Phone" required>
                                            <Input placeholder="Phone" value={data.phone} onChange={e => { handleChange(e.target.value, 'phone') }} />
                                        </Form.Item>
                                    </Col>
                                    <Col span={12}>
                                        <Form.Item label="Email" required>
                                            <Input placeholder="Email" value={data.email} onChange={e => { handleChange((e.target.value), 'email') }} />
                                        </Form.Item>
                                    </Col>
                                    <Col span={12}>
                                        <Form.Item label="Data of Birth" required>
                                            <DatePicker
                                                className="w-100"
                                                value={moment(data?.dateOfBirth)}
                                                format={'YYYY/MM/DD'}
                                                onChange={(v) => { handleChange((v), 'dateOfBirth') }} allowClear={false} />
                                        </Form.Item>
                                    </Col>
                                </Row>
                            </Col>
                            <Col span={4}>
                                <Form.Item label="Avatar">
                                    <UploadImage {...{ fileList, setFileList }} count={1} />
                                </Form.Item>
                            </Col>

                            <Col span={24}>
                                <Form.Item label="Address" required>
                                    <Input.TextArea placeholder="Address" value={data.address} onChange={e => { handleChange((e.target.value), 'address') }} />
                                </Form.Item>
                            </Col>
                            <Col span={10}>
                                <Form.Item label="Status" required>
                                    <Select value={data.status} onChange={v => { handleChange(v, 'status') }} >
                                        <Select.Option value={true}>Active</Select.Option>
                                        <Select.Option value={false}>Inactive</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                        </Row>
                    </Form>
                </Spin>
            </Modal>
        </>
    );
});


const  BookmarkModal = forwardRef((props, ref) => {
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });


    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(dt);
        }
    }));
    return (
        <>
            <Modal
                title="Bookmark Details"
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onCancel={() => { setOpen(false); }}
                maskClosable={false}
                width={1400}
                destroyOnClose
                className="app-modal-body-overflow"
                footer={null}
            >
                <Bookmarks dealerId={data._id}/>
            </Modal>
        </>
    );
});
const FollowersModal = forwardRef((props, ref) => {
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });


    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(dt);
        }
    }));
    return (
        <>
            <Modal
                title="Followers Details"
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onCancel={() => { setOpen(false); }}
                maskClosable={false}
                width={1400}
                destroyOnClose
                className="app-modal-body-overflow"
                footer={null}
            >
                <Followers dealerId={data._id}/>
            </Modal>
        </>
    );
});
