import { DashboardOutlined, UserOutlined, BankOutlined, ApartmentOutlined, CalendarOutlined } from '@ant-design/icons';
import Dashboard from '../pages/Dashboard';
import Model from '../pages/ListCar/Model';
import Brand from '../pages/ListCar/Brand';
import Application from "../pages/Application"
import Dealers from "../pages/Dealers";
import Car from '../pages/Car'
import Banner from '../pages/Banner';
import ProblemReport from '../pages/ProblemReport';
import Plan from '../pages/plan';


const routes = [
    { title: 'Dashboard', icon: DashboardOutlined, url: '/', component: Dashboard },
    { title: 'Dealers', icon: UserOutlined, url: '/dealers', component: Dealers },
    { title: 'Car', icon: UserOutlined, url: '/car', component: Car },
    { title: 'Banners', icon: ApartmentOutlined, url: '/banner', component: Banner },
    { title: 'Plans', icon: ApartmentOutlined, url: '/plan', component: Plan },


    {
        title: 'List Car', icon: BankOutlined, url: '/list', children: [
            { title: 'Brand', icon: ApartmentOutlined, url: '/', component: Brand },
            { title: 'Model', icon: ApartmentOutlined, url: '/model', component: Model },
        ]
    },

    { title: 'Problem Report', icon: CalendarOutlined, url: '/problemreport', component: ProblemReport },
    { title: 'Application', icon: CalendarOutlined, url: '/application', component: Application },

].filter(v => (!v.hidden) || v.children?.length);

export default routes;