import axios from "../utils/axios";

export default class service {

    static baseURL = 'car';

    static list(data) {
        return axios.get(this.baseURL + '/list', { params: data });
    }
    static listLikes(data) {
        return axios.get(this.baseURL + '/like/list', { params: data });
    }
    static detail(id, data) {
        return axios.get(this.baseURL + '/details/'+id, { params: data });
    }
    static saveOne(data) {
        return axios.post(this.baseURL + "/save-one", data);
    }
    static saveTwo(id,data) {
        return axios.post(this.baseURL + "/save-two/"+ id, data);
    }
    static saveThree(id,data) {
        return axios.post(this.baseURL + "/save-three/"+ id, data);
    }
    static saveFour(id,data) {
        return axios.post(this.baseURL + "/save-four/"+ id, data);
    }
    static delete(id) {
        return axios.post(this.baseURL + '/delete', { ids: id });
    }
}